import style from './style.module.css';
import { Link } from 'react-router-dom';

export function FooterSmall () {
    return <div className={style.main_wrap}>
    <span className={style.wrap__logo}> </span>
    <button className={style.wrap__btn}>  </button>


            <span><Link to="/creative.ct" className={style.wrap__link}></Link></span>
            <span><Link to="/rules" className={style.wrap__link}></Link></span>
            <span><Link to="/contests" className={style.wrap__link}></Link></span>
            <span><Link to="/about" className={style.wrap__link}> </Link></span>



</div>
}