import MediaQuery from 'react-responsive';
import { Footer } from '../../components/Footer/Footer';
import { FooterSmall } from '../../components/FooterSmall/FooterSmall';
import { HeaderMenuFull } from '../../components/HeaderMenuFull/HeaderMenuFull';
import { MenuHamb } from '../../components/MenuHamb/MenuHamb';
import style from './style.module.css';

export function RulesPage () {
    return <div className={style.ov}>
        <MediaQuery minWidth={700}>
            <HeaderMenuFull />
        </MediaQuery>
        <MediaQuery maxWidth={699}> 
            <MenuHamb/>
        </MediaQuery>

        <div className={style.container}>
        <div className={style.rules_cont}>
            <h2 className={style.container__h2}>
                <br/>     <span className={style.h2__span}></span>
            </h2>
            
            
                {/*rule 1 */}
                <span className={style.rule}>
                    <span className={style.rules__header}>
                    <p>  ?</p>
                    <p className={style.rules__span}>#_1</p>
                    </span>
                    <span className={style.rules__text}>
                    <p>          .     <p className={style.rules__span1}>.</p></p>
                    </span>
                </span>

                    <hr/>

                {/*rule 2 */}
                <span className={style.rule}>
                    <span className={style.rules__header}>
                    <p>     ?</p> 
                    <p className={style.rules__span}>#_2</p>
                    </span>
                    <span className={style.rules__text}>
                    <p>!           .</p>
                    </span>
                </span>

                    <hr/>

                {/*rule 3 */}
                <span className={style.rule}>
                    <span className={style.rules__header}>
                    <p>  ?</p> 
                    <p className={style.rules__span}>#_3</p>
                    </span>
                    <span className={style.rules__text}>
                    <p>        .</p>
                    </span>
                </span>

                    <hr/>

                {/*rule 4 */}
                <span className={style.rule}>
                    <span className={style.rules__header}>
                    <p className={style.attention}>#</p> 
                    </span>
                    <span className={style.rules__text}>
                    <p>     <span className={style.rules__span1}> ,</span>    ! </p>
                    </span>
                </span>
            </div>
        </div>
        <MediaQuery minWidth={576}>
            <Footer/>
        </MediaQuery>
        <MediaQuery maxWidth={575}> 
            <FooterSmall />
        </MediaQuery>
    </div>
}